* {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  overflow-x: hidden;
  position: relative;
  font-size: $font-size-base;
  font-family: $base-font-family;
  height: 100%;
  font-weight: 500;
}

img {
  object-fit: cover;
}

mark,
.mark {
  padding: 0.2em;
  background-color: rgba($secondary, 0.2);
}

.container,
.container-fluid,
.container-sm,
.container-md,
.container-lg,
.container-xl,
.container-xxl {
  @media screen and (max-width: 767px) {
    padding-left: $grid-gutter-width * 0.5;
    padding-right: $grid-gutter-width * 0.5;
  }
}

// Scrollbar

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 1px $gray-600;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: $secondary;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: darken($secondary, 10%);
}

.bg-blue-with-circle {
  * {
    z-index: 10;
  }
  padding-bottom: 3rem;
  position: relative;
  background-color: $primary;
  &::before,
  &::after {
    content: "";
    position: absolute;
    height: 100%;
    background-repeat: no-repeat;
    // z-index: 9;
    width: 33.313rem;
    @media screen and (max-width: 767px) {
      display: none;
    }
  }
  &::before {
    background-image: url("../../images/circles-top.svg");
    top: 0;

    #en & {
      left: -12px;
      background-position: top left;
      transform: scaleX(-1);
    }

    #ar & {
      right: 0;
      background-position: top right;
    }
  }

  &::after {
    background-image: url("../../images/circles-bottom.svg");
    bottom: 0;
    z-index: 9;
    #en & {
      right: -14px;
      background-position: bottom right;
      transform: scaleX(-1);
    }

    #ar & {
      left: 0;
      background-position: bottom left;
    }
  }
}


