@font-face {
  font-family: "Cairo";
  font-style: normal;
  font-weight: 200;
  src: url("#{$font-path}/cairo-font/extra-light.woff2") format("woff2");
  unicode-range: U+0600-06FF, U+200C-200E, U+2010-2011, U+204F, U+2E41,
    U+FB50-FDFF, U+FE80-FEFC;
}

