h1,
h2,
h3,
h4,
h5,
h6,
p,
small {
  @extend .mb-0;
  color: $primary;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  @extend .fw-bold;
  line-height: 1.3;
}

a {
  &:active,
  &:hover,
  &.active {
    color: $secondary;
  }
  &.active {
    font-weight: 700;
  }
}

.title-highlight {
  position: relative;
  z-index: 10;
  display: inline-block;
  &::before {
    content: "";
    background-color: $secondary;
    position: absolute;
    height: 1.313rem;
    left: 0;
    right: 0;
    top: 45%;
    z-index: -1;
    opacity: 0.2;
  }
}
